<template>
    <div :class="'mypage-container '+mobileClass">
        <div class="mypage-title">방송정보 보기</div>
        <div class="data-list-box">
                <ul class="data-list">
                    <li v-for="(item,index) in broadcastList" >
                        <div class="subject-content">
                            <div :class="'gtype '+item.status" >
                                {{item.statusName}}</div>
                            <div class="del-box" @click="deleteData(item.id,index)">
                                <font-awesome-icon style="color:#fff" icon="fas fa-times"></font-awesome-icon>
                            </div>
                           <div class="main" @click="articleView(item)">
                                <div class="subject">
                                    <div class="subject-text">{{ item.subject }}</div>

                                </div>
                                <div class="author-box">
                                    <div class="date">{{
                                        item.createAt.substring(0, 10)}}
                                    </div>

                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
        </div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_BROADCAST_LIVE_DELETE,
        API_URL_GET_MYPAGE_BROADCAST_LIST
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapActions} from "vuex";

    export default {
        name: 'memberMain',
        data() {
            return {
                broadcastList: [],
                total: 0,
                mobile: this.$isMobile(),
                mobileClass: ((this.$isMobile())?'mobile':''),

            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.getData();

            } else {
                this.$router.push('/');
            }

        },
        methods: {
            ...mapActions('broadcast', ["setGno"]),
            getData() {
                const frm = {};
                apiService.post(API_URL_GET_MYPAGE_BROADCAST_LIST, frm, (res) => {

                    if (res.status == 'success') {
                        for (let item of res.broadcastList) {
                            item.statusName = (item.status=='complete')?'방송종료':'방송중';
                            this.broadcastList.push(item)
                        }


                    }
                })
            },

            articleView(item) {
                this.setGno(item.id);
                this.$router.push('/video/setting/main')


            },
            deleteData(no, idx) {
                let message = {
                    type: 'confirm',
                    message: '선택된 정보를 삭제하시겠습니까?',
                    doAction: () => {
                        this.itemDelete(no, idx);
                    }
                };
                this.$eventBus.$emit('modalOpen', message);
            },
            itemDelete(no, idx) {
                const frm = {id: no.toString()}
                this.$eventBus.$emit('overlay', 'show');
                apiService.post(API_URL_BROADCAST_LIVE_DELETE, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.broadcastList.splice(idx, 1)
                    let params = {}

                    params = {
                        message: '정보가 삭제되었습니다',

                    };
                    this.$eventBus.$emit('modalOpen', params);


                })
            },
        }

    }
</script>


<style lang="scss" scoped>
    .mypage-container {
        width: 1200px;
        margin: auto;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .mypage-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .profile-box {
            padding: 50px 0 30px 0;
            text-align: center;
            border-bottom: solid 1px #ddd;

            .img-box {
                width: 96px;
                height: 96px;
                overflow: hidden;
                border: solid 1px #ddd;
                border-radius: 50%;
                margin: auto;

                .icon {
                    font-size: 40pt;
                    color: #ddd;
                    margin-top: 18px;
                }

                img {
                    width: 100%;
                    min-height: 96px;
                }
            }

            .user-name {
                font-size: 18px;
                color: #333;
                text-align: center;
                font-weight: 600;
                margin: 5px 0;
            }

            .whiteButton {
                width: 118px;
                height: 36px;
                margin: auto;
                max-height: 36px;

                span {
                    font-size: 13px !important;
                }
            }
        }

        .data-list-box {
            margin-top: 30px;

            .data-list {
                width: 100%;
                display: flex;
                flex-wrap: wrap;

                li {
                    -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                    -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                    box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                    width: 30%;
                    margin: 0 40px 40px 0;
                    height: 150px;
                    padding: 40px 0 0 30px;
                    border-radius: 10px;
                    position: relative;

                    &:nth-child(3n) {
                        margin-right: 0;
                    }
                    .del-box {
                        cursor: pointer;
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        background: #999999;
                        width: 20px;
                        height: 20px;
                        text-align: center;
                        line-height: 20px;
                        border-radius: 10px;

                        .icon {
                            font-size: 10pt;
                            color: #fff;
                        }
                    }

                }

                .gtype {
                    width: 60px;
                    height: 20px;
                    text-align: center;
                    line-height: 20px;
                    border-radius: 10px;
                    font-size: 9pt;;
                    color: #fff;
                    background: #2D8CFF;
                    &.complete {
                        background: #000;
                    }
                }

                .subject {
                    color: #000;
                    font-weight: bold;
                    padding: 10px 0;
                    font-size: 12pt;
                    cursor: pointer;
                }

                .date {
                    color: #999;
                    font-size: 10pt;
                    cursor: pointer;

                }
            }
        }

        &.mobile {
            width:100%;
            padding: 80px 20px 20px 20px;
            .data-list-box {
                .data-list {
                    width: 100%;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    padding:0;
                    li {
                        -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        width: 48%;
                        margin: 0 0 20px 0;
                        height: 100px;
                        padding:17px 0 0 10px;
                        border-radius: 10px;
                        position: relative;

                        .del-box {
                            cursor: pointer;
                            position: absolute;
                            right: 10px;
                            top: 10px;
                            background: #999999;
                            width: 20px;
                            height: 20px;
                            text-align: center;
                            line-height: 20px;
                            border-radius: 10px;

                            .icon {
                                font-size: 10pt;
                                color: #fff;
                            }
                        }
                    }

                    .gtype {
                        height: 20px;
                        text-align: center;
                        line-height: 20px;
                        border-radius: 10px;
                        font-size: 9pt;;
                        color: #fff;
                        background: #2D8CFF;
                        &.complete {
                            background: #000;
                        }
                    }

                    .subject {
                        color: #000;
                        font-weight: bold;
                        padding: 10px 0;
                        font-size: 12pt;
                        cursor: pointer;
                    }

                    .date {
                        color: #999;
                        font-size: 10pt;
                        cursor: pointer;

                    }
                }

            }
        }

    }


</style>
